    pesquisa: {
      "optica": [
        {
          "indice": 1,
          "linhaDePesquisa": "ptica Integrada",
          "texto": "Nesta linha de pesquisa focamos nossos trabalhos na chamada 'Fotnica do Silcio' (Silicon Photonics), ou mais especificamente, 'Nanofotnica do Silcio', que alm de ser um campo emergente de pesquisa e tecnologia  uma realidade de inovao presente em novos produtos. Nossos estudos incluem avanos em nano-dispositivos de silcio ou nitreto de silcio, tais como: guias de onda, ressonadores pticos e acopladores baseados em grades de Bragg. Dentro desta linha, destacam-se as simulaes computacionais em:",
          "pesquisas": [
            {
              "indice": 1,
              "titulo": "Transformada ptica e Invisibilidade",
              "texto": "Busca-se desenvolver novas estratgias para a realizao de mapeamentos, em ptica transformacional, que resultem em uma transformao cujo meio ptico resultante seja isotrpico ou com baixa anisotropia. Estudamos e investigamos diferentes transformaes de coordenadas para guias de onda planares, cujos objetivos so modificar a trajetria da onda eletromagntica e desenvolver dispositivos como guias de ondas, divisores de polarizao isotrpicos a mantos de invisibilidade.",
              "img": "assets/imgs/pesquisa/optica1.jpg"
            },
            {
              "indice": 2,
              "titulo": "Design e Modelagem de Nano Grades de Silcio",
              "texto": "O acoplamento ptico dos dispositivos nanofotnicos pode ser realizado via Grades de Bragg. A modelagem computacional dessas grades  fundamental para aumentar a eficincia de acoplamento entre os guias em nano-escalas e as convencionais fibras pticas de uma rede de telecomunicaes. Assim, nesta linha, simulamos novos arranjos estruturais dos guias, camadas refletoras e diferentes dopagens visando aumentar a eficincia de acoplamento.",
              "img": "assets/imgs/pesquisa/optica2.jpg"
            }
          ]
        }
      ],
      "fibraOptica": [
        {
          "indice": 2,
          "linhaDePesquisa": "Fibras pticas de Cristal Fotnico",
          "texto": "Nesta linha de pesquisa o foco  na investigao desta nova classe de fibras pticas, baseada no conceito de cristais fotnicos, denominada fibra ptica de cristal fotnico (PCF - Photonic Crystal Fiber) ou fibra microestruturada (MOF - Microstructured Optical Fiber). Sua geometria particular, com materiais de diferentes ndices de refrao estruturados de forma peridica, permite uma alta flexibilidade de projeto. Consegue-se, portanto, ajustar, conforme a necessidade, as propriedades de disperso, rea efetiva, perdas por confinamento, entre outras, o que torna estas fibras especialmente teis para aplicaes em comunicaes pticas. Dentro desta linha, destacam-se as simulaes computacionais em:",
          "pesquisas": [
            {
              "indice": 1,
              "titulo": "Sensoriamento ptico",
              "texto": "A disperso cromtica  um dos principais fatores limitantes nos atuais sistemas de transmisso de dados via fibra ptica. Logo,  fundamental o desenvolvimento de dispositivos pticos capazes de compensar esse indesejado efeito. As fibras pticas de cristal fotnico empregadas para compensao de disperso, devido  sua geometria, destacam-se pela sua alta maleabilidade no projeto de curvas de disperso. Nesta linha, buscamos manipular adequadamente os parmetros geomtricos e estruturais das PCFs para alterar, conforme necessidade e aplicao, os valores da disperso cromtica das fibras.",
              "img": "assets/imgs/pesquisa/imagemSensoriamentoOptico.jpg"
            }
          ]
        }
      ],
      "antenas": [
        {
          "indice": 3,
          "linhaDePesquisa": "Antenas e Propagao",
          "texto": "Nesta linha de pesquisa o foco  na investigao terica e prtica de novas antenas e arranjos de antenas capazes de atuarem, com mxima eficincia, alto ganho, apresentarem auto-sintonia e, tambm, podendo ser opticamente controladas. Com isso, podemos desenvolver antenas operando em banda larga e ou podendo operar em diferentes faixas de frequncias. Dentro desta linha, destacam-se as simulaes e experimentos em:",
          "pesquisas": [
            {
              "indice": 1,
              "titulo": "Transferncia de energia sem fios",
              "texto": "Sabe-se que a eficincia na transmisso de energia sem fios est diretamente relacionada com a distncia de separao entre o dispositivo transmissor e receptor. Ademais, a transferncia de energia  extremamente sensvel  frequncia de ressonncia de operao do sistema. Assim, buscamos desenvolver analtica, numrica, e experimentalmente dispositivos para transferncia de energia sem fios, auto-sintonizveis, capazes de operar sempre no ponto de mxima eficincia.",
              "img": "assets/imgs/pesquisa/imagemAntenaSemfio.jpg"
            },
            {
              "indice": 2,
              "titulo": "Antenas banda-larga assistidas por lentes",
              "texto": "Nesta linha, simulamos novos arranjos estruturais dos guias, camadas refletoras e diferentes dopagens visando aumentar a eficincia de acoplamento.",
              "img": "assets/imgs/pesquisa/imagemAntenaBanda2.jpg"
            },
            {
              "indice": 3,
              "titulo": "Antenas opticamente controladas",
              "texto": "Nesta aplicao, busca-se desenvolver antenas que apresentem padres reconfigurveis e controladas opticamente, operando na faixa de frequncia de onda milimtrica. Chaves de silcio so usadas para controlar a reconfigurao ptica, modificando a resposta em frequncia e o padro de radiao da antena. Portanto, projetamos e desenvolvemos novas estruturas e arranjos de antenas, controlados opticamente pela chave ptica, a qual apresenta resposta extremamente rpida. Este projeto  desenvolvido em parceria com o grupo WOCA do INATEL.",
              "img": "assets/imgs/pesquisa/imagemAntenaControlada.jpg"
            }
          ]
        }
      ],
      "terahertz": [
        {
          "indice": 4,
          "linhaDePesquisa": "Dispositivos em Terahertz",
          "texto": "Modelagem do efeito eletro-ptico em dispositivos LID, para aplicaes em moduladores que operam na faixa dos THz. O objetivo deste projeto  desenvolver um modulador eletro-ptico aplicando a tcnica de confinamento da onda eletromagntica no meio com menor ndice de refrao, atravs da descontinuidade na interface entre os meios, tal como ocorre nos dispositivos conhecidos como LIDs. Para tanto, investiga-se o efeito eletro-ptico em cristais anisotrpicos pertencentes a esta categoria de dispositivos pticos.",
          "pesquisas": []
        }
      ],
      "laiot": [
        {
          "indice": 5,
          "linhaDePesquisa": "LAIoT  Laboratrio de Aplicao em Internet Das Coisas",
          "texto": "Laboratrio recm-criado como um SpinOff do LabTel, para desenvolvimento de pesquisas na rea de RF e comunicaes. Especializado em conectividade entre mquinas e coisas, este laboratrio desenvolve pesquisa aplicada  eficincia energtica e alcance do sistema. Temos desenvolvido estudos prticos com as novas tecnologias IoT, destacando: LoRaWan, Sigfox e NB-IoT.",
          "pesquisas": []
        }
      ]
    },
